import { Base64 } from 'js-base64';

export default function authTablet() {
  document.querySelector('.js-auth-tablet').addEventListener('submit', (e) => {
    e.preventDefault();
    const login = document.querySelector('[name="login-auth"]');
    const pass = document.querySelector('[name="pass-auth"]');
    
    fetch('./reg/synchro', {
      method: 'POST',
      body: Base64.encode( JSON.stringify({
        userid: login.value, 
        content: JSON.stringify({
          type: 'f_auth',
          pass: pass.value
        })
      }))
    }).then(res => {
      if ( res.ok ) {
        res.json().then(obj => {
          if ( obj.code === 0 ) {
            if ( location.pathname === '/registration.html' ) {
              window.location.pathname = './install.html';
            }
          } else if ( obj.code === -1 ) {
            alert('Невірний логін або пароль!');
          }
        });
      }
      console.log(res);
    }).catch(error => {
      console.log(error);
    });
  });
}
authTablet();
